.main-navigation {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9999;
  width: 100%;
  height: 3.5rem;
  padding: 0 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
}

.main-navigation__logo h1 {
  margin: 0;
  font-size: 1.5rem;
}

.main-navigation__items {
  margin-left: 5rem;
}

.main-navigation__items ul {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  align-items: center;
}

.main-navigation__items li {
  margin: 0 1rem;
}

.main-navigation__items a,
.main-navigation__items button {
  text-decoration: none;
  color: white;
  padding: 0.25rem 0.5rem;
  border: none;
  font: inherit;
  background: transparent;
  cursor: pointer;
  vertical-align: middle;
  margin: 0;
}

.main-navigation__items a:hover,
.main-navigation__items a:active,
.main-navigation__items a.active,
.main-navigation__items button:hover,
.main-navigation__items button:active {
  background: #ffffff;
  color: #5101d1;
  border-radius: 5px;
}

.navbarBrandLogo {
  height: 4rem;
  display: inline-block;
  margin: 0 1rem;
}

.navbarMaster {
  text-align: center;
  padding-left: 9rem;
  border-bottom: 1px solid silver;
}

#home {
  background-color: rgba(233,137,134, .4);
}
#experience {
  background-color: rgba(255,255,255, .4);
}
#team {
  background-color: rgba(255,255,255, .4);
}
#path {
