.overlayTopdiv {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  text-align: center;
  background: rgba(0, 0, 0, 0.7);
  z-index: 5;
  padding: 7vh 0;
}
.overlayCol {
  width: 50vw;
  height: 80vh;
  margin: 0 auto;
  background-image: linear-gradient(rgba(197, 31, 113, .7),	rgba(236, 161, 48, .7));
  border-radius: 30%;
  padding: 1rem 3rem 3rem 3rem;
  overflow: scroll;
}
.overlayTopRow {
  /* float: right; */
  display: block;
}
.overlayRow {
  /* border: 1px solid #ffffff; */
